___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --primary-color: rgb(162, 210, 238);
    --primary-color-darker: rgb(1, 111, 175);
}

*{
    box-sizing: border-box;
    outline: 0;
    
}
body{
    margin: 0;
    padding: 0;
    background: var(--primary-color);
    font-family: 'Open sans', sans-serif;
    font-size: 1.3em;
    line-height: 1.5em;
}
.container{
    max-width: 640px;
    margin: 50px auto;
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    
}
form input, form label, form button{
    display: block;
    width: 100%;
    margin-bottom: 10px;
}
form input{
    font-size: 24px;
    height: 50px;
    padding: 0 20px;
}
form input:focus{
    outline: 1px solid var(var(--primary-color)) ;
}
form button{
    border: none;
    background: var(--primary-color);
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    height: 50px;
    cursor: pointer;
    margin-top: 30px;
}
form button:hover{
    background: var(--primary-color-darker);
}
.container h1{
    text-align: center;
    margin-bottom: 40px;
}

.cpf-gerado {
    font-size: 2em;
    color: var(--primary-color);
    text-align: center;
    font-weight: bold;
    margin-bottom: 40px;
    letter-spacing: 5px;
